.Sidebar {
    -webkit-font-smoothing: antialiased;
    text-size-adjust: 100%;
    --rem: 16;
    --google-font-color-materialiconstwotone: none;
    font-family: Roboto, Helvetica, Arial, sans-serif;
    font-size: 1.25rem;
    font-weight: 400;
    line-height: 1.625;
    letter-spacing: 0.00938em;
    white-space: nowrap;
    box-sizing: inherit;
    padding: 0px;
    color: rgba(233, 219, 219, 0.87);
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    z-index: 1200;
    position: fixed;
    top: 0px;
    height: calc(-2rem + 100vh);
    flex: 1 0 auto;
    outline: 0px;
    margin: 1rem;
    border-radius: 0.75rem;
    border-width: initial;
    border-style: none;
    border-color: initial;
    border-image: initial;
    background: linear-gradient(195deg, rgb(198, 205, 216), rgb(255, 255, 255)); /* צבעים חדשים */
    box-shadow: rgba(0, 0, 0, 0.05) 0rem 1.25rem 1.6875rem 0rem;
    margin-bottom: inherit;
    right: 0px; /* מיקום בצד ימין */
    width: 130px;
    transform: translateX(0px);
    transition: width 225ms cubic-bezier(0.4, 0, 0.6, 1), background-color 225ms cubic-bezier(0.4, 0, 0.6, 1);
}

.Sidebar ul {
    list-style-type: none; /* הסר את הנקודות */
    padding-left: 0; /* הסר ריווח פנימי */
    margin: 0; /* הסר ריווח חיצוני */
    display: flex;
    flex-direction: column;
    align-items: center; /* מרכז את כל הפריטים */
}

.Sidebar li {
    margin-bottom: 10px; /* ריווח בין הכפתורים */
    width: 100%; /* רוחב מלא של אלמנטי הרשימה */
    display: flex;
    justify-content: center; /* מרכז את הכפתורים בתוך אלמנטי הרשימה */
}


.Sidebar-button {
    font-family: 'Arial', sans-serif;
    font-size: 0.8rem;
    font-weight: bold;
    line-height: 1.5;
    letter-spacing: 0.05em;
    text-align: center;
    box-sizing: border-box;
    background: linear-gradient(135deg, #3a7bd5, #3a6073); /* שינוי צבעים לגוונים כחולים */
    color: #ffffff;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 90%;
    padding: 0.75rem 1rem;
    margin: 10px 0;
    border-radius: 15px; /* פינות פחות מעוגלות */
    cursor: pointer;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.25); /* שינוי הצללה */
    transition: all 0.4s ease; /* שינוי משך המעבר */
    text-transform: uppercase;
    text-decoration: none;
    position: relative; /* תכונה זו מאפשרת שימוש באפקטים פנימיים */
    overflow: hidden; /* שימוש באפקטים של מעבר צבע */
}

.Sidebar-button::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.1); /* שכבה קלה של אור */
    opacity: 0;
    transition: opacity 0.4s ease;
    border-radius: inherit; /* שמירה על הפינות המעוגלות */
}

.Sidebar-button:hover::before {
    opacity: 1;
}


.Sidebar-button:active {
    transform: translateY(1px); /* הורדת הכפתור קלות בעת לחיצה */
    box-shadow: 0 4px 20px rgba(243, 241, 241, 0.25); /* צללה כמו במצב הרגיל */
}



